.download {
    margin-top: 200px;
    margin-bottom: 100px;

    h1 {
        text-align: center;
        margin-bottom: 60px;
    }

    .download-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;

        li {
            border-radius: 15px;
            padding: 20px;
            cursor: pointer;
            background-image: linear-gradient(to left top, #668fff, #7a66ff);
            box-shadow: 15px 15px 1rem #00000029;
            margin-bottom: 60px;
            margin-left: 30px;
            margin-right: 30px;
            transition: .3s transform;

            &:hover {
                transform: scale(102%);
            }

            .icon {
                padding: 20px;
                border-radius: 100%;
                width: 150px;
                margin-bottom: 20px;

                svg {
                    width: 100%;
                }
            }

            h2 {
                text-align: center;
                color: #fff;
            }
        }
    }
}