.leaders {
    margin-top: 200px;
    margin-bottom: 200px;
    background-color: #fff;
    border: 1px dashed #bababa;
    border-radius: 15px;
    box-shadow: 15px 15px 1rem #00000010;

    .top {
        padding: 20px;
        border-bottom: 1px solid #bababa;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .filter {
            position: relative;

            .filter-btn {
                padding: 10px;
                border: 1px solid #bababa;
                font-size: 16px;
                border-radius: 10px;
                cursor: pointer;
                display: flex;
                align-items: center;
                box-shadow: 15px 15px 1rem #00000010;
                transition: .3s background-color;

                &:hover {
                    background-color: #eaeaea;
                }

                svg {
                    width: 21px;
                    margin-right: 10px;
                }
            }

            .dropdown-leaders-list {
                position: absolute;
                bottom: -170%;
                left: 0;
                background-color: #fff;
                font-size: 12px;
                display: none;

                &.active {
                    display: block;
                }

                button {
                    padding: 10px;
                    border: 1px solid #bababa;
                    width: 100%;
                    cursor: pointer;
                    transition: .3s background-color;

                    &:hover {
                        background-color: #eaeaea;
                    }
                }
            }
        }
    }

    .leaders-list {
        padding-bottom: 20px;

        li {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 20px;
            cursor: pointer;
            transition: .3s background-color;

            &:hover {
                background-color: #eaeaea;
            }

            img {
                width: 21px;
            }

            p {
                display: flex;
                align-items: center;

                img {
                    margin-right: 10px;
                }
            }
        }
    }
}

@media(max-width:600px) {
    .leaders {
        .top {
            padding: 20px;

            h2 {
                font-size: 16px;
            }
    
            .filter {
                .filter-btn {
                    font-size: 14px;
    
                    svg {
                        width: 18px;
                    }
                }
            }
        }
    }
}

@media(max-width:450px) {
    .leaders {
        .top {
            flex-direction: column;
            justify-content: left;
            align-items: start;

            h2 {
                font-size: 16px;
                margin-bottom: 10px;
            }
    
            .filter {
                .filter-btn {
                    font-size: 14px;
    
                    svg {
                        width: 18px;
                    }
                }
            }
        }
    }
}