header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 4;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        background-color: #fff;
        width: 100%;
        height: 100%;
        opacity: 0;
        visibility: hidden;
        transition: .3s;
    }
    
    &.active::before {
        opacity: 0.90;
        visibility: visible;
        transition: .3s;
    }
}

.header-body {
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo a {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 3;
    
    img {
        width: 50px;
        height: 50px;
        border-radius: 7px;
        margin-right: 15px;
    }
            
    span {
        color: #668fff;
        font-weight: 900;
        font-size: 24px;
    }
}

.header-nav {
    display: flex;
    align-items: center;
    position: relative;

    .header-list {
        display: flex;

        li {
            margin-right: 20px;

            a {
                font-size: 16px;
                color: #717171;
                transition: .3s color;

                &:hover {
                    color: #000;
                }
            }
        }

        .header-dropdown {
            margin-right: 20px;
            position: relative;

            .dropdown-text {
                display: flex;
                align-items: center;
                cursor: pointer;

                span {
                    color: #5f5f5f;
                    transition: .3s;
    
                    &:hover {
                        color: #000;
                    }
                }
    
                svg {
                    margin-left: 5px;
                    margin-bottom: 5px;
                }
            }

            .dropdown-menu {
                display: none;
            }

            &.active {
                .dropdown-menu {
                    display: block;
                    position: absolute;
                    top: 40px;
                    left: 0;
                    border: 1px solid #ddd;
                    border-radius: 7px;
                    min-width: 200px;
                    width: max-content;
                    max-width: 100vw;
                    z-index: 1;
                    background-color: #fff;
                }

                li {
                    margin-right: 0;

                    &:first-child a {
                        border-top-left-radius: 7px;
                        border-top-right-radius: 7px;
                    }
        
                    &:last-child a {
                        border-bottom-left-radius: 7px;
                        border-bottom-right-radius: 7px;
                    }
                    
                    a {
                        display: block;
                        padding: 10px;
                        text-decoration: none;
                        color: #656565;
                        white-space: nowrap;
    
                        &:hover {
                            background-color: #ddd;
                        }
                    }
                }
            }
        }
    }

    .dwnl-btn {
        padding: 10px 15px; 
        border-radius: 10px;
        background-color: #668fff;
        cursor: pointer;
        display: flex;
        align-items: center;
        transition: .3s background-color;
            
        &:hover {
            background-color: #4e6dc3;
        }
        
        svg {
            width: 16px;
            margin-right: 5px;
        }
        
        span {
            color: #fff;
        }
    }
}

@media (max-width:768px) {
    .hamburger {
        display: block;
        position: relative;
        width: 30px;
        height: 20px;
        position: relative;
        cursor: pointer;
        z-index: 4;

        &::before, &::after {
            content: "";
            background-color: #000;
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0;
            transition: 1s;
        }

        &::before {
            top: 0;
        }

        &::after {
            bottom: 0;
        }

        span {
            position: absolute;
            left: 0;
            width: 100%;
            height: 2px;
            top: 9px;
            background-color: #000;
            transition: 1s;
        }

        &.active::before {
            transform: rotate(45deg);
            top: 9px;
        }

        &.active::after {
            transform: rotate(-45deg);
            bottom: 9px;
        }

        &.active span {
            transform: scale(0);
        }
    }

    .header-nav {
        position: fixed;
        right: -100%;
        top: 0;
        background-color: #fff;
        height: 100vh;
        width: 250px;
        display: block;
        border-left: #b7b7b7 solid 3px;
        z-index: 3;
        transition: .3s right;

        &.active {
            right: 0;
        }

        .header-list {
            position: absolute;
            top: 80px;
            left: 20px;
            right: 20px;
            margin-left: 0;
            display: block;

            li {
                margin-left: 0;
                margin-right: 0;
                margin-bottom: 10px;
    
                a {
                    border-radius: 7px;
                    padding: 10px;
                    display: block;
                    width: 100%;
                    color: #000;
                    transition: .3s background-color;
    
                    &:hover {
                        background-color: #ddd;
                    }
                }
            }

            .header-dropdown {
                width: 100%;

                .dropdown-text {
                    margin-left: 0;
                    margin-right: 0;
                    margin-bottom: 10px;
                    padding: 10px;
                    border-radius: 7px;
                    width: 100%;
                    transition: .3s background-color;

                    &:hover {
                        background-color: #ddd;
                    }

                    span {
                        color: #000;
                    }
                }
            }
        }

        .dwnl-btn {
            position: absolute;
            bottom: 20px;
            right: 20px;
            left: 20px;
            // display: flex;
            // justify-content: right;
        }
    }
}