.records-window {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #000000;
    z-index: 5;
    background-color: #00000000;

    &::before {
        content: '';
        background-color: #000;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0.80;
    }

    .records-window-content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 20px;
        background-color: #fff;
        z-index: 5;
        border: 1px solid #ddd;
        border-radius: 15px;
        min-width: 400px;
    }

    h1, p {
        text-align: center;
    }

    h1 {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 32px;
            margin-right: 10px;
        }
    }

    p {
        margin-bottom: 20px;
    }

    button {
        text-align: right;
        width: 100%;
        cursor: pointer;
        margin-bottom: 20px;
        color: #595959;
        transition: .3s color;

        &:hover {
            text-decoration: underline;
            color: #000;
        }
    }

    a {
        color: #595959;
        transition: .3s color;

        &:hover {
            text-decoration: underline;
            color: #000;
        }
    }
}