.admins-item {
    display: flex;
    align-items: center;
    background-color: #e4efff;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 15px 15px 1rem #0000000f;

    img {
        width: 150px;
        border-radius: 15px;
        margin-right: 20px;
    }
}

@media(max-width:600px) {
    .admins-item {
        img {
            width: 100px;
            border-radius: 10px;
        }
    }
}